<template>
    <div class="main-content-wrap main-content-bg clearfix" v-wechat-title="VueTitle">
        
        <div class="w-1200 trend-main-wrap clearfix">
            <iframe :src="rebateUrl" scrolling="no" id="iframe" style="height:600px"></iframe>
        </div>

    </div>
</template>
<script>
import english from "../../assets/json/English.json";

export default {
    data() {
        return {
            english: english,
            VueTitle:english.天天返利,
            rebateUrl: "",
        }
    },
    created() {
        if (localStorage.getItem("token")) {
            this.rebateUrl = "https://www.appbale.net/index.php?m=api&c=app&a=rebate&token=" + localStorage.getItem("token");
        } else {
            this.$Modal.warning({
                content: english.您还未登录+'，'+english.请先登录,
                scrollable: true,
                onOk: () => {
                    var vm = this;
                    setTimeout(() => {
                        vm.$router.push({path:'/login'});
                    }, 1000);
                }
            });
        }
    },
    mounted() {
      
    },
    watch: {
        
    },
    computed: {
        
    },
    methods: {
       
    }
};
</script>
<style scoped>
.trend-main-wrap {
    padding: 35px;
    /* background: #f6f6f6; */
    border-radius: 8px;
}
#iframe {
    margin: 0 auto;
    display: block;
    padding: 0;
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 8px;
}
</style>


